import { Breadcrumbs, Button, Card, Grid, Link, Snackbar } from "@mui/material";
import Paper from "@mui/material/Paper";
import InputBase from "@mui/material/InputBase";
import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import SearchIcon from "@mui/icons-material/Search";
import ShareIcon from "@mui/icons-material/Share";
import React, { useState } from "react";

const DashboardPage = function () {
  const [open, setOpen] = useState(false);

  const handleClick = () => {
    setOpen(true);
    navigator.clipboard.writeText(window.location.toString());
  };

  return (
    <div>
      <Breadcrumbs
        sx={{ display: { xs: "none", sm: "block" } }}
        style={{
          backgroundColor: "#25274D",
          color: "white",
          padding: "1rem 5rem",
        }}
      >
        <Link href="#">Elocution</Link>
        <Link href="#">Home</Link>
      </Breadcrumbs>

      <div className="banner-container">
        <img
          src="/img/banner-image.jpeg"
          alt="banner"
          style={{ width: "100%", height: "300px" }}
        />
        <Card className="banner-card">
          <p>
            Lorem ipsum, dolor sit amet consectetur adipisicing elit. Laborum,
            ea. Error eaque amet repellendus tenetur. Doloremque sed dicta qui
            quos, laudantium voluptatibus fuga optio reiciendis, numquam, cum
            quia saepe deserunt.
          </p>
        </Card>
      </div>

      <div className="searchbar-container">
        <Paper
          component="form"
          sx={{
            p: "2px 4px",
            display: "flex",
            alignItems: "center",
            width: 460,
            boxShadow: 'none',
            border: '1px solid red',
            borderRadius: '6px'
          }}
        >
          <IconButton type="button" sx={{ p: "10px" }} aria-label="search">
            <SearchIcon style={{color: '#b71c1c'}}/>
          </IconButton>
          <InputBase
            sx={{ ml: 1, flex: 1 }}
            placeholder="Search Google Maps"
            // inputProps={handleInput}
          />
          <Divider style={{color: '#b71c1c'}} sx={{ height: 28, m: 0.5  }} orientation="vertical" />
          <Button variant="primary" sx={{ p: "10px", color: '#b71c1c' }}>
            SEARCH
          </Button>
        </Paper>
      </div>

      <div className="result-container">
        <Card className="result-card">
          <p className="result-text">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Laborum
            quas cupiditate tempora, explicabo repellendus non architecto eos
            doloribus voluptatem id, quod unde doloremque iure suscipit tenetur
            exercitationem magnam neque animi?
          </p>
          <div className="copy-btn">
            <Button>Copy</Button>
            <IconButton onClick={handleClick} color="primary">
              <ShareIcon />
            </IconButton>
            <Snackbar
              message="Copied to clibboard"
              anchorOrigin={{ vertical: "top", horizontal: "center" }}
              autoHideDuration={2000}
              onClose={() => setOpen(false)}
              open={open}
            />
          </div>
        </Card>
      </div>

      <div className="footer-container">
        <div className="footer-content">
          <Grid container>
            <Grid item xs={6}>
              <div className="main-text">YOUR PRIVACY IS MY IMPORTANT</div>
              <div className="sub-text">Start using Elocution today.</div>
              <Button>Feedback</Button>
              <Button>Contact Us {">"} </Button>
            </Grid>
            <Grid item xs={6}>
              <img
                src="/img/Sparkle.png"
                alt="banner"
                style={{ width: "100px", height: "100px" }}
              />
            </Grid>
          </Grid>
        </div>

        <div className="footer-content1">
          <Grid container>
            <Grid item xs={5}>
              <div className="main-text">Elocution</div>
              <div className="sub-text">Start using Elocution today.</div>
            </Grid>
            <Grid item xs={2}>
                <ul>
                    <li>About</li>
                    <li>Home</li>
                    <li>Test</li>
                </ul>
            </Grid>
            <Grid item xs={2}>
                <ul>
                    <li>Terms and condition</li>
                    <li>Privacy policy</li>
                    <li>Cookies policy</li>
                </ul>
            </Grid>
            <Grid item xs={3}>
                <ul>
                    <li>Lets chat</li>
                    <li>email</li>
                    <li>social icon</li>
                </ul>
            </Grid>
          </Grid>
        </div>
      </div>
    </div>
  );
};

export default DashboardPage;
